<template>
<div id="test" class='test' style="background-color:white">
  <div class='container'>
    <div class="row">
      <h2 style="float: left; width:1140px; margin-bottom: 15px;">GST Tax Invoice</h2>
      <h4 style="float: left; width:1140px; margin-bottom: 5px;">GST Tax Invoice</h4>
      <div style="float: right; margin-top:-40px; position:relative;">
        <b-dropdown text="Menu" left class="m-md-2">
          <b-dropdown-item @click="$root.$emit('show::modal','modal1')">My Company Details</b-dropdown-item>
          <b-dropdown-item @click="jsonExport()">Backup My Company Data</b-dropdown-item>
          <b-dropdown-item @click="$root.$emit('show::modal','modal2')">Restore My Company Data</b-dropdown-item>
          <b-dropdown-item @click="$root.$emit('show::modal','intro')">Introduction</b-dropdown-item>
        </b-dropdown>
      </div>
    </div>
    <div class="row">
      <h4 style="float: left; width:630px;">Invoice Id: {{invoiceIdComp}}</h4>
      <div>
        <h4> Date: </h4>
        <Datepicker format="dd-MM-yyyy" v-model='invoiceDate' style="float: left; margin-top: -35px; margin-left: 280px;" placeholder="Select Date"></Datepicker>
      </div>
    </div>
    <div class="row" style="margin-top: 15px;"> <datalist id="browsers">
      <option value="Internet Explorer"/>
      <option value="Firefox"/>
      <option value="Chrome"/>
      <option value="Opera"/>
      <option value="Safari"/>
    </datalist> <datalist id="browsers2">prdListArr
        <option v-for="cust in customerList" :value="cust.name1 + ' > ' + cust.gstin1 + ' > ' + cust.mobile1 + ' > ' + cust.email1 + ' > ' + cust.stateCode1 + ' > ' + cust.address1"></option>
    </datalist> <datalist id="prd">
        <option v-for="prd in prdListArr" :value="prd"></option>
    </datalist>
      <div class='group' style="float: left; width:230px"> <input class='ipc' maxlength=150 list="browsers2" v-model='name1' required type='text'> <span class='highlight'></span> <span class='bar'></span> <label>Cusomer Name</label> </div>
      <div class='group' style="float: left; width:230px"> <input class='ipc' maxlength=15 v-model='gstin1' required type='text'> <span class='highlight'></span> <span class='bar'></span><label>Customer GSTIN</label> </div>
      <div class='group' style="float: left; width:230px"> <input class='ipc' maxlength=15 v-model='mobile1' required type='text'> <span class='highlight'></span> <span class='bar'></span> <label>Cusomer Mobile</label> </div>
      <div class='group' style="float: left; width:230px"> <input class='ipc' maxlength=75 v-model='email1' required type='text'> <span class='highlight'></span> <span class='bar'></span><label>Customer Email</label> </div>
    </div>
    <div class="row" style="margin-top: 0px;">
      <div class='group' style="float: left; width:260px; margin-top: 30px;">
        <b-form-select v-model='stateCode1' :options='stateList' calss='mb-3' style="margin-top:-35px"></b-form-select>
      </div>
      <div class='group' style="float: left; width:460px; height:25 px; padding-left:10px; margin-top: 7px;">
        <b-form-input textarea v-model="address1" placeholder="Customer Address"></b-form-input>
      </div>
    </div>
    <div class="row">
      <div class="panel-body" id="appm">
        <table class="table table-hover" style="max-width:device-width;table-layout:fixed;text-align:left;margin: auto;">
          <thead>
            <tr>
              <th style="width: 30px; ">#</th>
              <th style="width: 165px; ">Description</th>
              <th style="width: 135px; ">HSN Code</th>
              <th style="width: 95px; ">Quantity</th>
              <th style="width: 95px; ">Price</th>
              <th style="width: 75px; ">Discount</th>
              <th style="width: 95px; ">GST Tax</th>
              <th style="width: 95px; ">Total</th>
              <th style="width: 90px; "></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(row, index) in rows " track-by="index ">
              <td>
                <div style="display:block ">{{ index +1 }}</div>
              </td>
              <td> <input class="form-control" maxlength=150 list="prd" v-model="row.description " /></td>
              <td> <input class="form-control" maxlength=50 v-model="row.hsn " /> </td>
              <td> <input class="form-control  text-right" maxlength=10 v-model="row.qty " type="number " min="0" step=".01" /> </td>
              <td> <input class="form-control  text-right" maxlength=20 v-model="row.price " type="number " min="0" step=".01" /> </td>
              <td> <input class="form-control  text-right" maxlength=2 v-model="row.discount " type="number " min="0" max="100 " step=".01" /> </td>
              <td> <select class="form-control " v-model="row.tax " v-on:change="taxCalc ">
                                        <option value="0 ">0%</option>
                                        <option value="5 ">5%</option>
                                        <option value="12 ">12%</option>
                                        <option value="18 ">18%</option>
                                        <option value="28 ">28%</option>
                                    </select>
                <ul style="font-size:13px">
                  <li v-show="row.cgst>0">CGST: {{row.cgst}}</li>
                  <li v-show="row.sgst>0">SGST: {{row.sgst}}</li>
                  <li v-show="row.igst>0">IGST: {{row.igst}}</li>
                  <li v-show="row.ugst>0">UTGST: {{row.ugst}}</li>
                </ul>
              </td>
              <td> <input class="form-control text-right" :value="row.qty * row.price" v-model="row.total" number readonly /> </td>
              <td> <button class="btn btn-danger btn-xs" @click="removeRow(index)"><icon name="trash" scale="1"></icon></button> </td>
            </tr>
          </tbody>
          <tfoot> <button class="btn btn-primary btn-xs" @click="addRow(index)">Add New Line</button>
            <tr>
              <td colspan="7" class="text-right">TAX</td>
              <td colspan="1" class="text-right">{{ taxtotal }}</td>
              <td></td>
            </tr>
            <tr>
              <td colspan="7" class="text-right">TOTAL</td>
              <td colspan="1" class="text-right">{{ total }}</td>
              <td></td>
            </tr>
            <tr>
              <td colspan="7" class="text-right"><strong>GRANDTOTAL</strong></td>
              <td colspan="1" class="text-right"><strong>{{ grandtotal }}</strong></td>
              <td></td>
            </tr>
          </tfoot>
        </table>
        <!-- <pre>{{ $data | json }}</pre> --></div>
    </div> <br>
    <b-btn @click="saveOrUpdate()" variant='primary'>Save</b-btn>
    <b-btn @click="clearInvTran()" variant='primary'>Cancel</b-btn>
    <hr />
    <div class="row">
      <h4 style="float: left; width:1140px; padding-bottom:15px">My Invoices</h4> </div>
    <div class="justify-content-centermy-1 row">
      <b-form-fieldset horizontal label="Rows per page" class="col-6" :label-size="6">
        <b-form-select :options="[{text:10,value:10},{text:20,value:20},{text:50,value:50}]" v-model="perPage"> </b-form-select>
      </b-form-fieldset>
      <b-form-fieldset horizontal label="Filter" class="col-6" :label-size="2">
        <b-form-input v-model="filter" placeholder="Type to Search"></b-form-input>
      </b-form-fieldset>
    </div>
    <!-- Main table element -->
    <b-table striped hover :items="invMstr" :fields="fields" :current-page="currentPage" :per-page="perPage" :filter="filter"> <template slot="index" scope="data">
        {{data.index + 1}}
      </template> <template slot="invoiceId" scope="item">
     {{item.value}}
   </template> <template slot="invoiceDate" scope="item">
     {{item.value | moment("DD-MM-YYYY")}}
   </template><template slot="name1" scope="item">
     {{item.value}}
   </template> <template slot="grandtotal" scope="item">
     {{item.value}}
   </template> <template slot="update" scope="item">
      <b-btn size="sm" @click="update(item.item)"><icon name="edit"></icon></b-btn>
    </template><template slot="print" scope="item">
       <b-btn size="sm" @click="print(item.item)"><icon name="print"></icon></b-btn>
     </template></b-table>
    <div class="justify-content-center row my-1">
      <b-pagination size="md" :total-rows="this.invMstr.length" :per-page="perPage" v-model="currentPage" /> </div>
    <!-- <b-btn style="margin-top: 0px;margin-right: -870px" @click="$root.$emit('show::modal','modal2')" variant='primary'>File import / Export</b-btn> -->
    <!-- Modal Component -->
    <b-modal id="modal1" size="md" title="Enter Company Details" ok-only @ok="submit" @shown="loadModalData">
      <form style="padding:5px">
        <div style="margin-left:-25px">
          <div class="row" style="margin-top: 5px;">
            <div class='group' style="float: left; width:200px"> <input class='ipc' v-model='cname' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'Name' }}</label> </div>
            <div class='group' style="float: left; width:200px"> <input class='ipc' v-model='cgstin' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'GSTIN' }}</label> </div>
          </div>
          <div class="row" style="margin-top: 5px;">
            <div class='group' style="float: left; width:200px"> <input class='ipc' v-model='invf' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'Invoice Fixed' }}</label> </div>
            <div class='group' style="float: left; width:200px"> <input class='ipc' v-model='invr' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'Invoice Serial' }}</label> </div>
          </div>
          <div class="row" style="margin-top: 5px;">
            <div class='group' style="float: left; width:200px"> <input class='ipc' v-model='cmobile' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'Mobile' }}</label> </div>
            <div class='group' style="float: left; width:200px"> <input class='ipc' v-model='cemail' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'Email' }}</label> </div>
          </div>
        </div>
        <div class="row" style="margin-top: 15px;">
          <b-form-select v-model='cstate' :options='stateList' calss='mb-3' style="margin-top:-35px"></b-form-select>&nbsp;
          <b-form-select v-model='ctaxIncl' :options='ynList' calss='mb-3' style="margin-top:-35px"></b-form-select>
        </div>
        <div class="row" style="margin-top: 5px;">
          <b-form-input textarea v-model="caddress" placeholder="Address"></b-form-input>
        </div>
        <div class="row" style="margin-top: 15px;">
          <b-form-input textarea v-model="termsCondn" placeholder="Terms & Conditions"></b-form-input>
        </div>
      </form>
    </b-modal>
    <b-modal id="intro" size="lg" title="Introduction" ok-only>
      <form>
        <b-card header="Starting Points" class="mb-2" title="Set Company Details" sub-title="For setup of company details you need to have Name, GSTIN (If applicable), Invoice numbers, State, Address. That all details will appear in print of invoice. Which can be setup by Menu > My Company Details."></b-card>
        <b-card header="Invoice" class="mb-2" title="Generate GST Invoice" sub-title="To generate invoice you need to fill customer details such as Name, GSTIN (If customer have), state, address, phone, email etc. <br/> After that add your product details, tax for the product, discount(in %) and save."></b-card>
        <b-card header="Report" class="mb-2" title="Invoice Report" sub-title="Generated invoices will be shown at the end of the page. You can search, update invoice & print invoice from there."></b-card>
        <b-card header="Data Security" class="mb-2" title="Backup & Restore of data" sub-title="Your data is yours.<br/> Backup from Menu > Backup My Company Data <br/> Restore from Menu > Restore My Company Data.<br/>This functionality will help you when you want to change the browser or device you are using."></b-card>
        <b-card header="GSTR" class="mb-2" title="GSTR1 Report" sub-title="GSTR1 report will be coming soon shortly before filing return of GST." show-footer> <small slot="footer" class="text-muted">
    Last updated 23-07-2017
  </small> </b-card>
      </form>
    </b-modal>
    <b-modal id="modal2" size="lg" title="Restore My Company Data" ok-only ok-title="Close">
      <b-alert variant="danger" show> Please use Restore My Company Data menu carefully. </b-alert>
      <b-alert variant="danger" show> Please make sure you are Restoring file that is downloaded from this website only. </b-alert>
      <b-alert variant="danger" show> Once you restore all current data will be lost and new restored data will be available. </b-alert>
      <form> Import Data <input type="file" @change="fileImport"><br/><br/> </form>
    </b-modal>
  </div>
</div>
</template>
<script>
import Vue from 'vue'
import AutocompleteVue from 'autocomplete-vue'
import Datepicker from 'vuejs-datepicker'
import swal from 'sweetalert2'
import VueMoment from 'vue-moment'
import Papa from 'papaparse'
// import localforage from 'vue-localforage'
Vue.component('Datepicker', Datepicker)
Vue.component('autocomplete-vue', AutocompleteVue)
import stateData from '../assets/state.json'
var stateList = stateData
export default {
  name: 'test',
  data: function () {
    return {
      gstin1: '',
      gstin2: '',
      gstinl: 'GSTIN  ',
      name1: '',
      name2: '',
      namel: 'Name  ',
      mobile1: '',
      mobile2: '',
      mobilel: 'Mobile  ',
      email1: '',
      email2: '',
      emaill: 'Email  ',
      address1: '',
      address2: '',
      addressl: 'Address  ',
      statel: 'State  ',
      stateCode1: null,
      stateCode2: null,
      cstate: '',
      stateCodel: 'State Code  ',
      invoiceId: '',
      invoiceIdl: 'Invoice No  ',
      invoiceDate: new Date(),
      invoiceDatel: 'Invoice Date  ',
      termsCondn: '',
      sameDet: true,
      stateList: stateList,
      customerList: [],
      ynList: [{
        'text': 'Excluded Tax',
        'value': false
      }, {
        'text': 'Included Tax',
        'value': true
      }],
      goodsnservice: 'G',
      taxtotal: 0,
      total: 0,
      rows: [{
        description: '',
        hsn: '',
        qty: 0,
        price: 0,
        total: 0,
        discount: 0,
        taxableAmt: 0,
        cgst: 0,
        sgst: 0,
        igst: 0,
        ugst: 0,
        tax: ''
      }],
      grandtotal: 0,
      showDismissibleAlert: false,
      cgstin: '',
      cname: '',
      cmobile: '',
      cemail: '',
      ctaxIncl: false,
      tranTaxIncl: false,
      caddress: '',
      invf: 'INV',
      invr: 1,
      invMstr: [],
      perPage: 10,
      filter: '',
      currentPage: 1,
      fields: {
        index: {
          label: '#',
          sortable: false
        },
        invoiceId: {
          label: 'Invoice Id',
          sortable: true
        },
        invoiceDate: {
          label: 'Invoice Date',
          sortable: true
        },
        name1: {
          label: 'Customer Name',
          sortable: true
        },
        grandtotal: {
          label: 'Total',
          sortable: true
        },
        update: {
          label: 'Update',
          sortable: false
        },
        print: {
          label: 'Print',
          sortable: false
        }
      },
      isUpdate: false,
      file: null,
      prdListArr: [],
      isNewCust: true,
      isNewPrd: true
    }
  },
  mounted: function () {
    var self = this
    // self.loadModalData()
    this.$getItem('companyData', function (err, value) {
      setTimeout(function () {
        if (err) {
          console.log(err.stack)
        }
        if (value !== null && value !== undefined) {
          self.cname = value.cname
          self.cgstin = value.cgstin
          self.caddress = value.caddress
          self.invf = value.invf
          self.invr = value.invr
          self.cstate = value.cstate
          self.termsCondn = value.termsCondn
          self.cmobile = value.cmobile
          self.ctaxIncl = value.ctaxIncl
          self.tranTaxIncl = value.ctaxIncl
          self.cemail = value.cemail
        }
      })
    }, 2000)
    this.loadInvMstr()
    this.loadCustomerMstr()
  },
  computed: {
    sameState: function () {
      var sstate = false
      if (this.stateCode1 === this.cstate) sstate = true
      return sstate
    },
    utState: function () {
      var sstate = false
      if (this.stateCode1 === 'DN' || this.stateCode1 === 'DD' || this.stateCode1 === 'LD' || this.cstate === 'AN' || this.cstate === 'CH') sstate = true
      return sstate
    },
    invoiceIdComp: function () {
      if (this.isUpdate) {
        return this.invoiceId
      } else {
        let f = this.invf
        let r = this.invr
        return f + r
      }
    }
  },
  methods: {
    convertNumberToWords: function (amount) {
      var words = []
      words[0] = ''
      words[1] = 'One'
      words[2] = 'Two'
      words[3] = 'Three'
      words[4] = 'Four'
      words[5] = 'Five'
      words[6] = 'Six'
      words[7] = 'Seven'
      words[8] = 'Eight'
      words[9] = 'Nine'
      words[10] = 'Ten'
      words[11] = 'Eleven'
      words[12] = 'Twelve'
      words[13] = 'Thirteen'
      words[14] = 'Fourteen'
      words[15] = 'Fifteen'
      words[16] = 'Sixteen'
      words[17] = 'Seventeen'
      words[18] = 'Eighteen'
      words[19] = 'Nineteen'
      words[20] = 'Twenty'
      words[30] = 'Thirty'
      words[40] = 'Forty'
      words[50] = 'Fifty'
      words[60] = 'Sixty'
      words[70] = 'Seventy'
      words[80] = 'Eighty'
      words[90] = 'Ninety'
      amount = amount.toString()
      var atemp = amount.split('.')
      var number = atemp[0].split(',').join('')
      var nLengh = number.length
      var wordsString = ''
      if (nLengh <= 9) {
        var nArray = [0, 0, 0, 0, 0, 0, 0, 0, 0]
        var nLenghreceivedNArray = []
        for (let i = 0; i < nLengh; i++) {
          nLenghreceivedNArray[i] = number.substr(i, 1)
        }
        for (let i = 9 - nLengh, j = 0; i < 9; i++, j++) {
          nArray[i] = parseInt(nLenghreceivedNArray[j])
        }
        for (let i = 0, j = 1; i < 9; i++, j++) {
          if (i === 0 || i === 2 || i === 4 || i === 7) {
            if (nArray[i] === 1) {
              nArray[j] = 10 + parseInt(nArray[j])
              nArray[i] = 0
            }
          }
        }
        let value = 0
        for (let i = 0; i < 9; i++) {
          if (i === 0 || i === 2 || i === 4 || i === 7) {
            value = parseInt(nArray[i]) * 10
          } else {
            value = parseInt(nArray[i])
          }
          if (value !== 0) {
            wordsString += words[value] + ' '
          }
          if ((i === 1 && value !== 0) || (i === 0 && value !== 0 && nArray[i + 1] === 0)) {
            wordsString += 'Crores '
          }
          if ((i === 3 && value !== 0) || (i === 2 && value !== 0 && nArray[i + 1] === 0)) {
            wordsString += 'Lakhs '
          }
          if ((i === 5 && value !== 0) || (i === 4 && value !== 0 && nArray[i + 1] === 0)) {
            wordsString += 'Thousand '
          }
          if (i === 6 && value !== 0 && (nArray[i + 1] !== 0 && nArray[i + 2] !== 0)) {
            wordsString += 'Hundred and '
          } else if (i === 6 && value !== 0) {
            wordsString += 'Hundred '
          }
        }
        wordsString = wordsString.split('  ').join(' ')
      }
      return wordsString
    },
    loadCustomerMstr: function () {
      var self = this
      self.customerList = []
      this.$getItem('customerMstr', function (err, value) {
        if (err) {
          console.log()
        } else {
          if (value !== null && value !== undefined) {
            self.customerList = value
          } else {
            // self.customerList = []
          }
        }
      })
    },
    fileImport: function (event) {
      var file = event.target.files[0]
      let self = this
      if (file) {
        new Promise(function (resolve, reject) {
          var reader = new FileReader()
          reader.onload = function (evt) {
            resolve(evt.target.result)
          }
          reader.readAsText(file)
          reader.onerror = reject
        }).then(function (result) {
          self.$setItem('invoiceMstr', JSON.parse(result))
          self.$root.$emit('hide::modal', 'modal2')
          self.$nextTick(function () {
            swal({
              title: '<h3>Data Loaded Successfully.</h3>',
              type: 'success'
            })
            self.loadInvMstr()
            self.file = null
          })
        }).catch(function (err) {
          console.log(err)
        })
      }
      /* var files = evt.target.files
      var file = files[0]
      var inputData
      var reader = new FileReader()
      reader.onload = function (event) {
        console.log(event.target.result)
      }
      inputData = reader.readAsText(file)
      console.log(inputData) */
    },
    jsonExport: function () {
      var csvData = new Blob([JSON.stringify(this.invMstr)], {
        type: 'text/csv;charset=utf-8;'
      })
      var csvURL = null
      let fname = this.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss') + '.txt'
      if (navigator.msSaveBlob) {
        csvURL = navigator.msSaveBlob(csvData, fname)
      } else {
        csvURL = window.URL.createObjectURL(csvData)
      }
      var tempLink = document.createElement('a')
      tempLink.href = csvURL
      tempLink.setAttribute('download', fname)
      tempLink.click()
    },
    downloadCsv: function () {
      var config = {
        delimiter: ',',
        header: true,
        skipEmptyLines: true,
        dynamicTyping: true,
        error: function (err) {
          console.log(err)
        }
      }
      var csv = Papa.unparse(this.invMstr, config)
      var csvData = new Blob([csv], {
        type: 'text/csv;charset=utf-8;'
      })
      var csvURL = null
      if (navigator.msSaveBlob) {
        csvURL = navigator.msSaveBlob(csvData, VueMoment(new Date()).format('YYYY-MM-DD HH:mm:ss') + '.csv')
      } else {
        csvURL = window.URL.createObjectURL(csvData)
      }
      var tempLink = document.createElement('a')
      tempLink.href = csvURL
      tempLink.setAttribute('download', 'download.csv')
      tempLink.click()
      /* var blob = new Blob(this.invMstr)
      var a = window.document.createElement('a')
      a.href = window.URL.createObjectURL(blob, {
        type: 'text/plain'
      })
      a.download = VueMoment(new Date()).format('YYYY-MM-DD HH:mm:ss') + '.csv'
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a) */
    },
    update: function (value) {
      this.isUpdate = true
      this.gstin1 = value.gstin1
      this.gstin2 = value.gstin2
      this.name1 = value.name1
      this.name2 = value.name2
      this.email1 = value.email1
      this.email2 = value.email2
      this.mobile1 = value.mobile1
      this.mobile2 = value.mobile2
      this.address1 = value.address1
      this.address2 = value.address2
      this.stateCode1 = value.stateCode1
      this.stateCode2 = value.stateCode2
      this.rows = value.rows
      this.invoiceId = value.invoiceId
      this.invoiceDate = value.invoiceDate
      this.tranTaxIncl = value.tranTaxIncl
      this.taxCalc()
      window.scrollTo(0, 0)
    },
    loadModalData: function () {
      let self = this
      this.$getItem('companyData', function (err, value) {
        if (err) {
          console.log(err.stack)
        }
        if (value !== null && value !== undefined) {
          self.cname = value.cname
          self.cgstin = value.cgstin
          self.caddress = value.caddress
          self.invf = value.invf
          self.invr = value.invr
          self.cstate = value.cstate
          self.termsCondn = value.termsCondn
          self.cmobile = value.cmobile
          self.ctaxIncl = value.ctaxIncl
          self.cemail = value.cemail
        }
      })
    },
    incrInvId: function () {
      if (!this.isUpdate) {
        let self = this
        this.$getItem('companyData', function (err, value) {
          if (err) {
            console.log(err.stack)
          }
          if (value !== null && value !== undefined) {
            value.invr = parseFloat(value.invr) + 1
            self.$setItem('companyData', value)
            self.invf = value.invf
            self.invr = value.invr
          }
        })
      }
    },
    loadInvMstr: function () {
      var self = this
      this.$getItem('invoiceMstr', function (err, value) {
        if (err) {
          console.log()
        } else {
          if (value !== null && value !== undefined) {
            self.invMstr = value
          } else {
            self.invMstr = []
          }
        }
      })
      self.invMstr.forEach(function (inv) {
        inv.rows.forEach(function (invDet) {
          if (self.prdListArr.indexOf(invDet.description + ' > ' + invDet.hsn) === -1) self.prdListArr.push(invDet.description + ' > ' + invDet.hsn)
        })
      })
    },
    saveOrUpdate: function () {
      if (this.isUpdate) {
        this.updateInv()
      } else {
        this.saveInv()
      }
    },
    updateInv: function () {
      let self = this
      this.invMstr.forEach(function (inv) {
        if (inv.invoiceId === self.invoiceId) {
          inv.gstin1 = self.gstin1
          inv.gstin2 = self.gstin2
          inv.name1 = self.name1
          inv.name2 = self.name2
          inv.mobile1 = self.mobile1
          inv.mobile2 = self.mobile2
          inv.email2 = self.email2
          inv.email1 = self.email1
          inv.address1 = self.address1
          inv.address2 = self.address2
          inv.stateCode1 = self.stateCode1
          inv.stateCode2 = self.stateCode2
          inv.cstate = self.cstate
          inv.invoiceIdComp = self.invoiceIdComp
          inv.invoiceDate = self.invoiceDate
          inv.sameDet = self.sameDet
          inv.taxtotal = self.taxtotal
          inv.total = self.total
          inv.rows = self.rows
          inv.sameState = self.sameState
          inv.invoiceIdComp = self.invoiceIdComp
          inv.grandtotal = self.grandtotal
        }
      })
      let tempCust = true
      self.invMstr.forEach(function (inv) {
        if (inv.name1 === self.name1 && inv.gstin1 === self.gstin1 && inv.mobile1 === self.mobile1) {
          tempCust = false
        }
      })
      if (tempCust) {
        this.$getItem('customerMstr', function (err, value) {
          if (err) {
            console.log(err.stack)
          }
          let gstin1 = this.gstin1
          let name1 = this.name1
          let mobile1 = this.mobile1
          let email1 = this.email1
          let address1 = this.address1
          let stateCode1 = this.stateCode1
          var custJson = {
            gstin1,
            name1,
            mobile1,
            email1,
            address1,
            stateCode1
          }
          if (value !== null && value !== undefined) {
            value.push(custJson)
            self.$setItem('customerMstr', value)
          } else {
            let tempArr = []
            tempArr.push(custJson)
            self.$setItem('customerMstr', tempArr)
          }
        })
      }
      self.$setItem('invoiceMstr', this.invMstr)
      self.$nextTick(function () {
        swal({
          title: '<h3>Invoice Id: <b>' + this.invoiceIdComp + '</b> Updated Successfully.</h3>',
          type: 'success'
        })
        self.clearInvTran()
        // self.loadInvMstr()
      })
    },
    saveInv: function () {
      let temp = false
      let self = this
      let no = self.invoiceIdComp
      let tempCust = true
      self.invMstr.forEach(function (inv) {
        if (inv.invoiceId === no) {
          temp = true
        }
        if (inv.name1 === self.name1 && inv.gstin1 === self.gstin1 && inv.mobile1 === self.mobile1) {
          tempCust = false
        }
      })
      if (temp) {
        swal({
          title: '<h3>Invoice Id: ' + no + ' already exists. </br>Please Update Invoice Serial Number</h3>',
          type: 'warning'
        })
        self.$nextTick(function () {
          self.$root.$emit('show::modal', 'modal1')
        })
      } else {
        let gstin1 = this.gstin1
        let gstin2 = this.gstin2
        let name1 = this.name1
        let name2 = this.name2
        let mobile1 = this.mobile1
        let mobile2 = this.mobile2
        let email1 = this.email1
        let email2 = this.email2
        let address1 = this.address1
        let address2 = this.address2
        let stateCode1 = this.stateCode1
        let stateCode2 = this.stateCode2
        let cstate = this.cstate
        let invoiceId = this.invoiceIdComp
        let invoiceDate = this.invoiceDate
        let sameDet = this.sameDet
        let taxtotal = this.taxtotal
        let total = this.total
        let rows = this.rows
        let sameState = this.sameState
        let invoiceIdComp = this.invoiceIdComp
        let grandtotal = this.grandtotal
        let goodsnservice = this.goodsnservice
        let tranTaxIncl = this.tranTaxIncl
        let self = this
        this.$getItem('invoiceMstr', function (err, value) {
          if (err) {
            console.log(err.stack)
          }
          var invJson = {
            gstin1,
            gstin2,
            name1,
            name2,
            mobile1,
            mobile2,
            email1,
            email2,
            address1,
            address2,
            stateCode1,
            stateCode2,
            cstate,
            invoiceId,
            invoiceDate,
            sameDet,
            taxtotal,
            total,
            rows,
            sameState,
            invoiceIdComp,
            grandtotal,
            goodsnservice,
            tranTaxIncl
          }
          if (value !== null && value !== undefined) {
            value.push(invJson)
            self.$setItem('invoiceMstr', value)
          } else {
            let tempArr = []
            tempArr.push(invJson)
            self.$setItem('invoiceMstr', tempArr)
          }
          self.$nextTick(function () {
            swal({
              title: '<h3>Invoice Created Successfully With Invoice Id: <b>' + this.invoiceIdComp + '</b></h3>',
              type: 'success'
            })
            self.clearInvTran()
            self.incrInvId()
            self.loadInvMstr()
          })
        })
        if (tempCust) {
          this.$getItem('customerMstr', function (err, value) {
            if (err) {
              console.log(err.stack)
            }
            var custJson = {
              gstin1,
              name1,
              mobile1,
              email1,
              address1,
              stateCode1
            }
            if (value !== null && value !== undefined) {
              value.push(custJson)
              self.$setItem('customerMstr', value)
            } else {
              let tempArr = []
              tempArr.push(custJson)
              self.$setItem('customerMstr', tempArr)
            }
          })
        }
      }
    },
    clearInvTran: function () {
      this.gstin1 = ''
      this.gstin2 = ''
      this.name1 = ''
      this.name2 = ''
      this.email1 = ''
      this.email2 = ''
      this.mobile1 = ''
      this.mobile2 = ''
      this.address1 = ''
      this.address2 = ''
      this.stateCode1 = null
      this.stateCode2 = null
      this.taxtotal = 0
      this.total = 0
      this.invoiceDate = new Date()
      this.rows = [{
        description: '',
        hsn: '',
        qty: 0,
        price: 0,
        total: 0,
        discount: 0,
        taxableAmt: 0,
        cgst: 0,
        sgst: 0,
        igst: 0,
        ugst: 0,
        tax: ''
      }]
      this.isUpdate = false
      this.isNewCust = true
      this.isNewPrd = true
    },
    saveInvTran: function (tempArr) {
      this.$setItem('invoiceMstr', tempArr)
    },
    submit: function (e) {
      let temp = false
      let self = this
      let no = self.invf + self.invr
      self.invMstr.forEach(function (inv) {
        if (inv.invoiceId === no) {
          temp = true
        }
      })
      if (temp) {
        swal({
          title: '<h3>Invoice Id: ' + no + ' already exists. </br>Please Update Invoice Serial Number</h3>',
          type: 'warning'
        })
        self.$nextTick(function () {
          self.$root.$emit('show::modal', 'modal1')
        })
      } else {
        let cname = this.cname
        let cgstin = this.cgstin
        let caddress = this.caddress
        let invf = this.invf
        let invr = this.invr
        let cstate = this.cstate
        let termsCondn = this.termsCondn
        let cmobile = this.cmobile
        let ctaxIncl = this.ctaxIncl
        this.tranTaxIncl = this.ctaxIncl
        let cemail = this.cemail
        this.$setItem('companyData', {
          cname,
          cgstin,
          caddress,
          cstate,
          invf,
          invr,
          termsCondn,
          cmobile,
          cemail,
          ctaxIncl
        })
      }
    },
    addRow: function (index) {
      try {
        this.rows.push({
          description: '',
          hsn: '',
          qty: 0,
          price: 0,
          total: 0,
          discount: 0,
          taxableAmt: 0,
          cgst: 0,
          sgst: 0,
          igst: 0,
          ugst: 0
        })
      } catch (e) {
        console.log(e)
      }
    },
    removeRow: function (index) {
      this.rows.splice(index, 1)
    },
    truncateDecimal: function (val) {
      if (isNaN(val)) val = 0
      let tempVal = parseFloat(val) + 0
      return Math.round(parseFloat(tempVal) * 100) / 100
      // let tempVal = parseFloat(val) + 0.00
      // return Math.round(parseFloat(tempVal) * 100) / 100
    },
    truncateForm: function (val) {
      val = val + ''
      if (isNaN(val)) val = '0'
      if (val.indexOf('.') !== val.length - 1) val = Math.round(parseFloat(val) * 100) / 100
      return val
    },
    taxCalc: function () {
      var t = 0
      var tt = 0
      var sstate = this.sameState
      var utState = this.utState
      var self = this
      this.rows.forEach(function (modRw) {
        if (modRw.description.indexOf('>') !== -1) {
          let fullArr = modRw.description.split(' > ')
          self.isNewPrd = false
          modRw.description = fullArr[0]
          modRw.hsn = fullArr[1]
          self.$nextTick(function () {
            self.isNewPrd = true
          })
        }
        if (self.isNewPrd) {
          let found = false
          for (var i = 0; i < self.prdListArr.length; i++) {
            let prd2 = self.prdListArr[i]
            if (prd2.split(' > ')[0].toLowerCase() === modRw.description.toLowerCase()) {
              modRw.description = prd2.split(' > ')[0]
              modRw.hsn = prd2.split(' > ')[1]
              found = true
            }
          }
          if (!found) {
            modRw.hsn = ''
          }
        }
        modRw.qty = self.truncateForm(modRw.qty || 0)
        modRw.price = self.truncateForm(modRw.price || 0)
        if (modRw.discount > 100 || modRw.discount < 0) {
          modRw.discount = 0
        } else {
          modRw.discount = self.truncateForm(modRw.discount || 0)
        }
        let lineTotal = modRw.qty * modRw.price
        let lineDisc = modRw.discount > 0 ? (lineTotal * modRw.discount) / 100 : 0
        let netLineTotal = lineTotal - lineDisc
        let lineTax = modRw.tax === undefined || modRw.tax === '' ? 0 : parseFloat(modRw.tax)
        if (self.tranTaxIncl) {
          modRw.taxableAmt = self.truncateDecimal(netLineTotal * lineTax / (100 + lineTax))
        } else {
          modRw.taxableAmt = self.truncateDecimal(netLineTotal * lineTax / 100)
        }
        modRw.total = self.truncateDecimal(netLineTotal)
        if (sstate) {
          let amt = self.truncateDecimal(modRw.taxableAmt / 2)
          if (utState) {
            modRw.sgst = 0
            modRw.ugst = amt
          } else {
            modRw.sgst = amt
            modRw.ugst = 0
          }
          modRw.igst = 0
          modRw.cgst = amt
        } else {
          modRw.cgst = 0
          modRw.sgst = 0
          modRw.igst = modRw.taxableAmt
          modRw.ugst = 0
        }
        t += modRw.total
        tt += modRw.taxableAmt
      })
      this.taxtotal = isNaN(tt) ? 0 : this.truncateDecimal(tt)
      this.total = this.truncateDecimal(t)
      if (this.tranTaxIncl) {
        this.grandtotal = this.truncateDecimal(this.total)
      } else {
        this.grandtotal = this.truncateDecimal(this.taxtotal + this.total)
      }
    },
    print: function (value) {
      var self = this
      var mywindow = window.open('', 'PRINT', 'height=800,width=1000')
      var rowsDataA = value.rows
      let state = ''
      for (var ik = 0; ik < stateData.length; ik++) {
        let sd = stateData[ik]
        if (value.stateCode1 === sd.value) {
          state = sd.text
          break
        }
      }
      if (value.mobile1 === undefined || value.mobile1 === '' || value.mobile1 === null) {
        value.mobile1 = '--'
      }
      if (value.cmobile === undefined || value.cmobile === '' || value.cmobile === null) {
        value.cmobile = '--'
      }
      if (value.cemail === undefined || value.cemail === '' || value.cemail === null) {
        value.cemail = '--'
      }
      var htmlData =
        '<style>body{border: 1px solid black; text-align: center}.gti-table{border: solid 1px #000; border-collapse: collapse; max-width: 1250px; table-layout: fixed; text-align: left; margin: auto;}.gti-table thead th{font-size: 0.8em; border: solid 1px #000; padding: 2px;}.gti-table tbody td{padding: 2px; font-size: 0.8em; color: #333; border-right: solid 1px #000; border-left: solid 1px #000;}.inner{float: left; width: 100%;}.inner2{float: left; width: 100%;}.inner3{float: left; width: 100%;}.h1{margin-bottom: 0;}@media (min-width: 500px){.inner{width: 50%;}.inner2{width: 40%;}.inner3{width: 60%;}</style><html><head> <meta name="viewport" content="width=device-width,initial-scale=1,maximum-scale=1,user-scalable=no"> <meta http-equiv="X-UA-Compatible" content="IE=edge,chrome=1"> <meta name="HandheldFriendly" content="true"> <title>www.gsttaxinvoice.com</title></head><body> <h3>GST TAX INVOICE</h3> <h2>' +
        self.cname + '</h2> <div> GSTIN - ' + self.cgstin + '</div><div>' + self.caddress + '</div><div> Mobile - ' + self.cmobile + ' Email - ' + self.cemail + '</div><div>Invoice Id : ' + value.invoiceId + '</div><div>Invoice Date: ' + self.$moment(
          value.invoiceDate).format('DD-MM-YYYY') + '</div><br/> <div style="width: 100%;"> <div class="inner"> <div><b>Customer Details</b> </div><div><b>' + value.name1 + '</b> </div><div>' + value.stateCode1 + ' - ' + state + '</div><div>' +
        value.address1 + '</div><div>Mobile : ' + value.mobile1 + '</div><div>&nbsp;</div></div><div class="inner"> <div><b>Consignee Details</b> </div><div><b>' + value.name1 + '</b> </div><div>' + value.stateCode1 + ' - ' + state + '</div><div>' +
        value.address1 + ' <div>Mobile : ' + value.mobile1 +
        '</div></div></div></div><table class="gti-table"> <thead> <tr> <th style="width: 30px; ">#</th> <th style="width: 165px; ">Description</th> <th style="width: 135px; ">HSN Code</th> <th style="width: 95px; ">Quantity</th> <th style="width: 95px; ">Unit Price </th> <th style="width: 95px; ">Total </th> <th style="width: 75px; ">Discount </th> <th style="width: 75px; ">Taxable Value </th> <th style="width: 95px; ">GST Tax </th> </tr></thead> <tbody>'
      var rowStr = ''
      for (var i = 0; i < rowsDataA.length; i++) {
        var row = rowsDataA[i]
        let rwTax = ''
        if (row.tax === undefined) {
          rwTax = '0'
        } else {
          if (row.sgst > 0) rwTax += 'SGST@' + row.tax / 2 + '% - ' + row.sgst + '</br>'
          if (row.ugst > 0) rwTax += 'UTGST@' + row.tax / 2 + '% - ' + row.ugst + '</br>'
          if (row.cgst > 0) rwTax += 'CGST@' + row.tax / 2 + '% - ' + row.cgst + '</br>'
          if (row.igst > 0) rwTax += 'IGST@' + row.tax + '% - ' + row.igst
        }
        rowStr += '<tr><td>' + (i + 1) + '</td>'
        rowStr += '<td>' + row.description + '</td>'
        rowStr += '<td>' + row.hsn + '</td>'
        rowStr += '<td>' + row.qty + '</td>'
        rowStr += '<td>' + row.price + '</td>'
        rowStr += '<td>' + row.total + '</td>'
        rowStr += '<td>' + row.discount + '</td>'
        rowStr += '<td>' + (row.total) + '</td>'
        rowStr += '<td style="font-size:10px">' + rwTax + '</td>'
        rowStr += '</tr>'
      }
      htmlData += rowStr
      htmlData += ' </tbody> </table><br/><div style="width: 100%; float:left;"><div style="width: 60%; float:left;text-align:left; padding-left:5px;white-space: pre-wrap;"><b>Terms & Conditions: </b><br/>' + self.termsCondn +
        '</div><div style="width: 35%; float:right;text-align:left"><div style="width: 100%;"><div class="inner2"><div>Tax Total</div> <div>Total</div> <div>Grand Total </div></div><div class="inner3"><div><b>₹ ' + value.taxtotal +
        '</b></div><div><b>₹ ' + value.total + '</b></div><div><b>₹ ' + value.grandtotal + '</b></div><br/><div style="width:100%;float:left;text-align:center;font-size:15px"></div></div></div></div></div><br/><div><b><br/><br/>Amount(In words): ' +
        self.convertNumberToWords(value.grandtotal) + ' Only</b><div style="width:95%;text-align: right"><b><br/><br/>Authorised Signatory</b></div></div></body></html>'
      mywindow.document.write(htmlData)
      /* mywindow.document.write('<style>body    { border: 5px solid black }</style>')
      mywindow.document.write('<html><head><title>' + document.title + '</title>')
      mywindow.document.write('</head><body >')
      mywindow.document.write('<h1>' + document.title + '</h1>')
      mywindow.document.write(document.getElementById('appm').innerHTML)
      mywindow.document.write('</body></html>') */
      mywindow.document.close() // necessary for IE >= 10
      mywindow.focus() // necessary for IE >= 10*/
      mywindow.print()
      mywindow.close()
      return true
    }
  },
  watch: {
    invr: function (val, oldVal) {
      if (isNaN(this.invr)) this.invr = oldVal
    },
    cstate: function () {
      this.taxCalc()
      this.loadInvMstr()
    },
    sameDet: function () {
      if (this.sameDet) {
        this.name2 = this.name1
        this.gstin2 = this.gstin1
        this.stateCode2 = this.stateCode1
        this.address2 = this.address1
        this.email2 = this.email1
        this.mobile2 = this.mobile1
      }
    },
    name1: function () {
      if (this.name1.indexOf('>') !== -1) {
        let fullArr = this.name1.split(' > ')
        this.isNewCust = false
        this.name1 = fullArr[0]
        this.gstin1 = fullArr[1]
        this.mobile1 = fullArr[2]
        this.email1 = fullArr[3]
        this.stateCode1 = fullArr[4]
        this.address1 = fullArr[5]
        this.$nextTick(function () {
          this.isNewCust = true
        })
      }
      let self = this
      if (self.isNewCust) {
        let found = false
        self.customerList.forEach(function (cust) {
          if (self.name1.toLowerCase() === cust.name1.toLowerCase()) {
            self.name1 = cust.name1
            self.gstin1 = cust.gstin1
            self.mobile1 = cust.mobile1
            self.email1 = cust.email1
            self.stateCode1 = cust.stateCode1
            self.address1 = cust.address1
            found = true
          }
        })
        if (!found) {
          self.gstin1 = ''
          self.mobile1 = ''
          self.email1 = ''
          self.stateCode1 = null
          self.address1 = ''
        }
      }
      if (this.sameDet) {
        this.name2 = this.name1
      }
    },
    mobile1: function () {
      if (this.sameDet) {
        this.mobile2 = this.mobile1
      }
    },
    email1: function () {
      if (this.sameDet) {
        this.email2 = this.email1
      }
    },
    gstin1: function () {
      if (this.sameDet) {
        this.gstin2 = this.gstin1
      }
    },
    stateCode1: function () {
      if (this.sameDet) {
        this.stateCode2 = this.stateCode1
      }
      this.taxCalc()
    },
    address1: function () {
      if (this.sameDet) {
        this.address2 = this.address1
      }
      this.taxCalc()
    },
    rows: {
      handler: function (event) {
        this.taxCalc()
      },
      deep: true
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
* {
  box-sizing: border-box;
}


/* basic stylings ------------------------------------------ */

.container {
  font-family: 'Roboto';
  display: block;
  margin-top: -55px;
  background: #FFF;
}

h2 {
  text-align: center;
  margin-bottom: 50px;
}

h2 small {
  font-weight: normal;
  color: #888;
  display: block;
}


/* form starting stylings ------------------------------- */

.group {
  position: relative;
  margin-bottom: 45px;
  margin-left: 40px
}

.ipc {
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 220px;
  border: none;
  border-bottom: 1px solid #757575;
}

.ipc:focus {
  outline: none;
}


/* LABEL ======================================= */

label {
  color: #999;
  font-size: 18px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}


/* active state */

.ipc:focus~label,
.ipc:valid~label {
  top: -20px;
  font-size: 14px;
  color: #5264AE;
}


/* BOTTOM BARS ================================= */

.bar {
  position: relative;
  display: block;
  width: 220px;
}

.bar:before,
.bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 1px;
  position: absolute;
  background: #5264AE;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.bar:before {
  left: 50%;
}

.bar:after {
  right: 50%;
}


/* active state */

input:focus~.bar:before,
input:focus~.bar:after {
  width: 50%;
}


/* HIGHLIGHTER ================================== */

.highlight {
  position: absolute;
  height: 60%;
  width: 100px;
  top: 25%;
  left: 0;
  pointer-events: none;
  opacity: 0.5;
}


/* active state */

input:focus~.highlight {
  -webkit-animation: inputHighlighter 0.3s ease;
  -moz-animation: inputHighlighter 0.3s ease;
  animation: inputHighlighter 0.3s ease;
}


/* ANIMATIONS ================ */

@-webkit-keyframes inputHighlighter {
  from {
    background: #5264AE;
  }
  to {
    width: 0;
    background: transparent;
  }
}

@-moz-keyframes inputHighlighter {
  from {
    background: #5264AE;
  }
  to {
    width: 0;
    background: transparent;
  }
}

@keyframes inputHighlighter {
  from {
    background: #5264AE;
  }
  to {
    width: 0;
    background: transparent;
  }
}

td {
  padding-bottom: 10px;
}

,
h1,
h2 {
  font-weight: bold;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

.table th {
  text-align: center;
  padding: 0.25rem;
}
</style>



